import Vue from "vue";
import Vuex from "vuex";
Vue.use(Vuex);
//scss bootstrop vue vuex 
//Sass

//1.
//
//2.
//
//3.
//
//4.
//
//vuex  store  store  store
var state={
    title:"",
    musicList:[],
    hashM:"",
    urlM:"",
    musicM:"",
    songName:"",
    singerName:"",
    singIndex:null,
    singIndex1:null,
    singPage:2
}
var mutations={
    change(state,newTitle){
        state.title = newTitle
    },
    addList(state,arr){
        state.musicList = arr;
    },
    addSongs(state,arr){
        state.musicList = state.musicList.concat(arr);
        state.singPage=state.singPage+1
    },
    hash(state,index){
        state.hashM=state.musicList[index].hash; 
        state.singIndex=index+1
        state.singIndex1=index-1
        //console.log(state.hashM);
    },
    addImg(state,url){
        state.urlM=url.replace("{size}/","")
        console.log(state.urlM)
    },
    addMusic(state,url){
        state.musicM=url
    },
    title(state,url){
        state.songName=url;
    },
    addsinger(state,url){
        state.singerName=url;
    }
}

var store= new Vuex.Store({
    state,
    mutations
})
export default store


